/* global chrome cast */

const loadedURLs = []

function loadScript(url) {
  if (loadedURLs.includes(url)) return

  let script = document.createElement('script')
  script.setAttribute('src', url)
  document.head.appendChild(script)
  loadedURLs.push(url)
}

const initializeCastApi = function(applicationId) {
  cast.framework.CastContext.getInstance().setOptions({
    receiverApplicationId: applicationId,
    autoJoinPolicy: chrome.cast.AutoJoinPolicy.ORIGIN_SCOPED
  })
  //   cast.framework.CastContext.getInstance().setOptions({
  //     receiverApplicationId: chrome.cast.media.DEFAULT_MEDIA_RECEIVER_APP_ID
  //   })
}

let _senderInitialized = false
export function initSender(applicationId) {
  if (_senderInitialized) return
  window['__onGCastApiAvailable'] = function(isAvailable) {
    if (isAvailable) {
      initializeCastApi(applicationId)
    }
  }

  loadScript(
    'https://www.gstatic.com/cv/js/sender/v1/cast_sender.js?loadCastFramework=1'
  )
  _senderInitialized = true
}

let _receiverInitialized = false
export function initReceiver(callback) {
  if (_receiverInitialized) return
  loadScript(
    '//www.gstatic.com/cast/sdk/libs/caf_receiver/v3/cast_receiver_framework.js'
  )
  _receiverInitialized = true
  waitNotNull(
    function() {
      console.log('checking...')
      return cast.framework.CastReceiverContext.getInstance()
    },
    function(ctx) {
      console.log('started!')
      callback(ctx)
    },
    300
  )
}

export function setMediaURL(url) {
  waitNotNull(
    function() {
      return cast.framework.CastContext.getInstance().getCurrentSession()
    },
    function(castSession) {
      var mediaInfo = new chrome.cast.media.MediaInfo(url, 'text/html')
      var request = new chrome.cast.media.LoadRequest(mediaInfo)
      castSession.loadMedia(request).then(
        function() {
          console.log('Load succeed')
        },
        function(errorCode) {
          console.log('Error code: ' + errorCode)
        }
      )
    },
    1000
  )
}

function waitNotNull(func, callback, timeout = 1000) {
  var ret = null
  try {
    ret = func()
  } catch {
    //
  }
  if (ret == null) {
    setTimeout(waitNotNull, timeout, func, callback)
  } else {
    callback(ret)
  }
}
