<template>
  <google-cast-launcher />
</template>

<script>
import * as castApi from '@/cast'
export default {
  props: {
    url: {
      type: String,
      required: true
    },
    mime: {
      type: String,
      required: true
    }
  },
  mounted() {
    const applicationId = '3297334C'
    castApi.initSender(applicationId)
    //castApi.setMediaURL(window.location.href, 'text/html')
    castApi.setMediaURL(this.url, this.mime)
  }
}
</script>
