<template>
  <div>
    <div class="scripture">
      <!-- <div v-for="verse in verses" 
:key="verse.verse"> -->
      <p class="verse-text" 
v-html="verse.text" />
      <p class="verse-verse">
        {{ verse.verse }}
        <span v-if="verse.trans" 
class="translation">({{ verse.trans }})</span>
      </p>
      <!-- <cast-button
        v-if="showCastButton"
        class="cast-container"
        url="//mechdude.github.io/encourage/favicon.png"
        mime="image/png"
      /> -->
      <cast-button
        v-if="showCastButton"
        class="cast-container"
        url="http://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4"
        mime="video/mp4"
      />
    </div>
    <button @click="next()">&rarr;</button>
    <!-- </div> -->
  </div>
</template>

<script>
import CastButton from '@/components/CastButton.vue'
export default {
  components: {
    CastButton
  },
  props: {
    verses: {
      type: Array,
      required: true
    },
    showCastButton: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      idx: 0
    }
  },
  computed: {
    seq() {
      return this.verses
        .map((v, idx) => [idx, Math.random()])
        .sort((a, b) => b[1] - a[1])
        .map(a => a[0])
    },
    verse() {
      const v = this.verses[this.seq[this.idx]]
      if (v == undefined) debugger
      return v || {}
    }
  },
  mounted() {
    this.tmpIdx = Math.floor(Math.random() * this.verses.length)
    this._interval = setInterval(() => this.next(), 5 * 60 * 1000)
  },
  destroyed() {
    clearInterval(this._interval)
  },
  methods: {
    next() {
      this.idx = (this.idx + 1) % this.verses.length
    }
  }
}
</script>

